/* Import Syne font */
@import url('https://fonts.googleapis.com/css2?family=Syne:wght@400..800&display=swap');

/* Custom styles */
body {
  font-family: 'Syne', sans-serif !important;
  margin: 0;
  padding: 0;
}

.navbar-custom {
  position: fixed;
  /* Fixes the navbar at the top */
  top: 0;
  /* Align to the top */
  left: 0;
  /* Stretch to the left edge */
  width: 100%;
  /* Ensure the navbar spans the full width */
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 35px;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
  z-index: 1000;
  /* Ensures the navbar stays on top of other elements */
}

.navbar-brand {
  font-size: 12px;
  font-weight: 700;
}

.nav-links a {
  margin-right: 40px;
  text-decoration: none;
  font-size: 14px;
  color: #000;
  font-weight: 600;
  position: relative;
  /* Ensures ::after positions relative to this parent */
}

.nav-links a::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -1px;
  /* Moves the underline slightly below the text */
  width: 0;
  height: 1px;
  background: #000000;
  /* Initial underline color */
  transition: width 0.4s ease, background-color 0.4s ease;
  /* Smooth animation */
}

.nav-links a:hover {
  color: #000000;
  /* Ensures text color doesn't change unexpectedly */
}

.nav-links a:hover::after {
  width: 100%;
  /* Expand the underline to full width */
  background-color: #ffcc00;
  /* Change underline color to yellow */
}

.connect-btn {
  background-color: #000;
  color: #fff;
  font-weight: 600;
  border: none;
  padding: 8px 12px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.connect-btn:hover {
  background-color: #333;
  /* Slightly lighter black on hover */
}

.navbar-line {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 120px;
  height: 63px;
  width: 1px;
  background-color: #000;
  margin: 0 20px;
}

.navbar-line-two {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 230px;
  height: 63px;
  width: 1px;
  background-color: #000;
  margin: 0 20px;
}

.connect-btn {
  background-color: #000;
  color: #fff;
  padding: 11px 22px;
  font-weight: 600;
  font-size: 12px;
  border: 1px solid black !important;
  border: none;
  cursor: pointer;
}

.connect-btn:hover {
  background-color: transparent;
  color: #000;
  padding: 11px 22px;
  font-weight: 600;
  font-size: 12px;
  border: 1px solid black !important;
  border: none;
  cursor: pointer;
}

.menu-icon {
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

.menu-icon div {
  width: 20px;
  height: 2px;
  background-color: #000;
  margin: 3px 0;
}

@media (max-width: 768px) {
  .nav-links {
    display: none;
  }

  .menu-icon {
    display: flex;
  }

  .navbar-line {
    display: none;
  }

  .navbar-line-two {
    display: none;
  }
}

.banner-title {
  font-weight: 700;
}

.banner-description {
  color: #7f7f7f;
}

.banner-content {
  padding: 100px !important;
  margin-top: 0px !important;
}

@media (max-width: 768px) {
  .banner-content {
    padding: 40px !important;
    margin-top: -110px !important;
  }
}

.line {
  margin-top: 0;
  margin-bottom: 0;
  background-color: #000;
  color: #000;

  border: 0.01px solid #000 !important;
}

.banner-btn {
  background-color: #000;
  color: #fff;
  padding: 14px 40px;
  font-weight: 600;
  font-size: 12px;
  border: 1px solid black !important;
  border: none;
  cursor: pointer;
}

.banner-btn:hover {
  background-color: transparent;
  color: #000;
  border: 1px solid black !important;
}

.banner-btn-two {
  background-color: #00000000;
  color: #000000;
  padding: 14px 40px;
  font-weight: 600;
  font-size: 12px;
  border: 1px solid black !important;
  border: none;
  cursor: pointer;
}

.banner-btn-two:hover {
  background-color: black;
  color: #ffffff;
  border: 1px solid black !important;
}

.works-section {
  padding: 40px 0;
}

.works-title {
  font-size: 2.5rem;
  margin-bottom: 3rem;
  font-weight: 500;
}

.work-item {
  border: 0.01px solid #000000;
  padding: 2rem;
  height: 100%;

  background: #fff;
  transition: all 0.3s ease;
}

.work-item:hover {
  background: #f8f8f8;
}

.work-image-wrapper {
  position: relative;
  width: 100%;
  aspect-ratio: 1;
  overflow: hidden;
  margin-bottom: 1.5rem;
}

.work-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.work-item:hover .work-image {
  transform: scale(1.05);
}

.work-title {
  font-size: 1.25rem;
  margin-bottom: 0.75rem;
  font-weight: 500;
}

.work-description {
  font-size: 0.875rem;
  color: #666;
  line-height: 1.6;
}

/* Remove gutters from grid to achieve connected boxes */
.works-grid {
  --bs-gutter-x: 0px !important;
  --bs-gutter-y: 0px !important;
  background-color: #ffffff;
}

.custom-container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.section {
  margin-top: 70px;
  margin-bottom: 50px;
}

.underline-button {
  font-size: 13px;
  color: #000000;
  /* White text for good contrast */
  text-decoration: none;
  position: relative;
  padding: 5px 0;
  font-weight: 700;
  letter-spacing: 1px;
  transition: color 0.3s ease;
  /* Smooth color transition on hover */
}

.underline-button::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 3px;
  background: #000000;
  /* A rich orange for a more sophisticated touch */
  transition: width 0.4s ease, background-color 0.4s ease;
  /* Smooth animation for width and color change */
}

.underline-button:hover {
  color: #000000;
  /* Change text color to match the underline */
}

.underline-button:hover::after {
  width: 100%;
  background-color: #ffcc00;
  /* Change underline color on hover for a smooth transition */
}

.skills-section {
  width: 100%;
  background-color: #f9f9f9;
}

.skills-title {
  font-size: 2rem;
  margin-bottom: 1rem;
  font-weight: 700;
}

.skills-description {
  font-size: 1rem;
  color: #444;
  line-height: 1.6;
}

.skills-cell {
  border: 0.01px solid #000;
  /* Light border */
  text-align: center;
  padding: 35px;
  /* Increased padding */
  font-size: 1.1rem;
  font-weight: 500;
  color: #333;

  img {
    width: 60px;
  }
}

.skills-cell:hover {
  background-color: #000000;
  color: #fff;

  cursor: pointer;
}

@media (max-width: 768px) {
  .skills-title {
    font-size: 1.8rem;
  }

  .skills-description {
    font-size: 0.9rem;
  }

  .skills-cell {
    padding: 15px;
    /* Adjust padding for smaller screens */
    font-size: 1rem;
  }
}

.footer-cell {
  border: 0.01px solid #000;
  /* Light border */
  text-align: center;
  padding: 165px;
  /* Increased padding */
  font-size: 1.1rem;
  font-weight: 500;
  color: #333;

  img {
    width: 60px;
  }
}

.footer-content-cell {
  border: 0.01px solid #000;
  /* Light border */
  text-align: left;
  padding: 65px;
  /* Increased padding */
  font-size: 1.1rem;
  font-weight: 500;
  color: #333;

  .content {
    padding-left: 20px;
  }
  .desc {
    font-size: 12px;
  }
  a {
    text-decoration: none;
    color: #000;
    font-size: 12px;
    font-weight: 200;
  }
}
